<template>
  <div class="taxi-page">
    <HeaderNav />
    <div class="taxi-page__content">
      <SkeletonPageStub v-if="isLoading" />
      <div class="taxi-page__container" v-else>
        <TitleWithChartCard class="taxi-page__income-chart" />
        <TitleWithChartCard class="taxi-page__orders-chart" />
        <TotalSection
          class="taxi-page__income"
          :title="totalIncome"
          caption="прибыль"
        >
          <img src="./assets/ruble.jpg" alt="" />
        </TotalSection>
        <TotalSection
          class="taxi-page__orders"
          :title="totalOrders"
          caption="поездок"
        >
          <img src="./assets/people.jpg" alt="" />
        </TotalSection>
        <TopDriversCard class="taxi-page__top-drivers" :drivers="topDrivers" />
        <CatalogSection
          class="taxi-page__catalog"
          title="Водители"
          :quantity="totalDrivers"
          href="/drivers"
        >
          <img src="./assets/driver.png" alt="" />
        </CatalogSection>
        <TotalSection
          class="taxi-page__rating"
          :title="averageScore"
          caption="средняя оценка"
        >
          <img src="./assets/star.svg" alt="" />
        </TotalSection>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import HeaderNav from '@/components/header-nav/header-nav.vue';
import TitleWithChartCard from '@/components/title-with-chart-card/title-with-chart-card.vue';
import TotalSection from '@/components/total-section/total-section.vue';
import TopDriversCard from '@/components/top-drivers-card/top-drivers-card.vue';
import CatalogSection from '@/components/catalog-section/catalog-section.vue';
import { taxiRouteNames } from '@/router/helpers';
import pageStore, { TAXI_PAGE_STORE } from '@/store/taxi-page';
import convertCurrency from '@/helpers/convert-currency';
import SkeletonPageStub from '@/components/skeleton-page-stub/skeleton-page-stub.vue';

const storeModule = {
  name: TAXI_PAGE_STORE,
  module: pageStore,
};

export default {
  components: {
    HeaderNav,
    TitleWithChartCard,
    TotalSection,
    TopDriversCard,
    CatalogSection,
    SkeletonPageStub,
  },

  data: () => ({ isLoading: false }),

  beforeCreate() {
    if (!this.$store.hasModule(storeModule.name)) {
      this.$store.registerModule(storeModule.name, storeModule.module);
    }
  },

  async created() {
    this.isLoading = true;
    await this.LOAD_DATA();
    this.isLoading = false;
  },

  beforeDestroy() {
    this.$store.unregisterModule(storeModule.name);
  },

  methods: {
    ...mapActions(storeModule.name, ['LOAD_DATA']),
  },

  computed: {
    taxiRouteNames() {
      return taxiRouteNames;
    },

    pageState() {
      return this.$store.state[storeModule.name];
    },

    totalIncome() {
      return convertCurrency(this.pageState.totalIncome, '₽');
    },

    totalOrders() {
      return this.pageState.totalOrders;
    },

    topDrivers() {
      return this.pageState.topDrivers;
    },

    totalDrivers() {
      return this.pageState.totalDrivers;
    },

    averageScore() {
      return this.pageState.averageScore;
    },
  },
};
</script>

<style>
.taxi-page {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: auto;
  padding-bottom: 90px;
}

.taxi-page__content {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 1200px;
  margin: 100px auto 0;
}

.taxi-page__container {
  flex-grow: 1;
  display: grid;
  grid-gap: 25px;
  grid-template-columns: repeat(5, 1fr);
  grid-template-areas:
    'incomeChartCard incomeChartCard incomeChartCard incomeCard incomeCard'
    'incomeChartCard incomeChartCard incomeChartCard ordersCard ordersCard'
    'ordersChartCard ordersChartCard ordersChartCard topDriversCard topDriversCard'
    'catalogCard catalogCard ratingCard topDriversCard topDriversCard';
}
.taxi-page__income-chart {
  grid-area: incomeChartCard;
}
.taxi-page__orders-chart {
  grid-area: ordersChartCard;
}
.taxi-page__income {
  grid-area: incomeCard;
}
.taxi-page__orders {
  grid-area: ordersCard;
}
.taxi-page__top-drivers {
  grid-area: topDriversCard;
}
.taxi-page__catalog {
  grid-area: catalogCard;
}
.taxi-page__rating {
  grid-area: ratingCard;
}
</style>
