import Vue from 'vue';
import Vuex from 'vuex';
import TaxiService from '@/services/taxi-page';

Vue.use(Vuex);

export const TAXI_PAGE_STORE = 'TAXI_PAGE_STORE';

export default {
  namespaced: true,
  state: {
    totalIncome: 0,
    totalOrders: 0,
    topDrivers: [],
    totalDrivers: 0,
    averageScore: 0,
  },
  actions: {
    async LOAD_DATA({ commit }) {
      const { data } = await TaxiService.getData();

      commit('SET_DATA', data);
    },
  },

  mutations: {
    SET_DATA(state, payload) {
      state.totalIncome = payload.totalIncome;
      state.totalOrders = payload.totalOrders;
      state.topDrivers = payload.topDrivers;
      state.totalDrivers = payload.totalDrivers;
      state.averageScore = payload.averageScore;
    }
  },

};
