<template>
  <UiContainer class="top-drivers-card">
    <div class="top-drivers-card__header">
      <UiTextString size="xxl" weight="700">Таблица лидеров</UiTextString>
      <ButtonLink>смотреть все →</ButtonLink>
    </div>

    <div class="top-drivers-card__top">
      <TopDriver :driver="topDriver" />
    </div>

    <div class="top-drivers-card__list">
      <TopDriverItem
        class="top-drivers-card__driver"
        v-for="(driver, index) in topDrivers"
        :key="driver.id"
        :number="index + 1"
        :src="driver.src"
        :name="driver.name"
        :rating="driver.rating"
      />
    </div>
  </UiContainer>
</template>

<script>
import UiContainer from '@/components/core/ui-container/ui-container.vue';
import ButtonLink from '@/components/button-link/button-link.vue';
import UiTextString from '@/components/core/ui-text-string/ui-text-string.vue';
import TopDriver from '@/components/top-driver/top-driver.vue';
import TopDriverItem from '@/components/top-driver-item/top-driver-item.vue';

export default {
  components: {
    UiContainer,
    ButtonLink,
    TopDriver,
    UiTextString,
    TopDriverItem,
  },

  props: {
    drivers: {
      type: Array,
      required: true,
    },
  },

  computed: {
    topDriver() {
      return this.drivers[0] || {};
    },

    topDrivers() {
      return this.drivers.slice(1, this.drivers.length - 1);
    },
  },
};
</script>

<style>
.top-drivers-card__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.top-drivers-card__top {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.top-drivers-card__driver {
  margin-top: 16px;
}
</style>
